@import 'src/styles/helpers.scss';

.list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.item {
  position: relative;
  z-index: 0;

  display: flex;
  align-items: center;

  cursor: pointer;
  letter-spacing: -0.02em;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + #{rem(12px)});
    z-index: 1;

    height: 2px;

    opacity: 0;
    background-color: color(blue);
    transition: $transition;
  }

  &.active {
    color: color(blue);

    &::before {
      opacity: 1;
    }
  }

  &:not(:first-child) {
    margin-left: rem(32px);
  }
}

.text {
  @include fsl(24px, 90%);
}

.count {
  margin-left: rem(12px);

  @include fsl(18px, 90%);
}