@import 'src/styles/helpers.scss';

.wrapper {
  margin-top: rem(20px);
}

.heading {
  margin: 0 auto;
  max-width: calc(#{$col-part-d} * 8 - #{$col-gap-d});

  letter-spacing: -0.04em;
  text-align: center;
  @include fsl(40px, 120%);
}

.inner {
  margin-top: rem(80px);
}

.row {
  display: flex;
  justify-content: space-around;
}

.card {
  width: rem(910px);
}

.link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text {
  color: #A6A6A6;
  letter-spacing: -0.05em;
  @include fsl(68px, 90%);
}

.icon {
  margin-left: rem(16px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(24px)};
    margin-top: rem(5px);

    width: var(--size);
    height: var(--size);

    path {
      fill: #A6A6A6;
    }
  }
}