@import 'src/styles/helpers.scss';

.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1111;

  width: 100vw;
  height: 100vh;

  pointer-events: none;

  &.active {
    pointer-events: all;

    .overlay {
      opacity: .3;
    }

    .container {
      transform: none;
    }
  }
}

.overlay {
  width: 100%;
  height: 100%;

  background: #000000;
  opacity: 0;
  transition: $transition;
}

.container {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  padding: rem(40px);
  transform: translateX(100%);

  width: rem(950px);
  height: 100%;

  background-color: color(white);
  transition: $transition;
}

.title {
  margin-bottom: rem(40px);

  text-align: left;
}

.text {
  margin-left: auto;

  width: rem(445px);

  font-weight: 400;
  @include fsl(20px, 140%);
  letter-spacing: -0.02em;
}

.form {
  margin-top: rem(60px);
}

.grid  {
  margin-bottom: rem(12px);

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: rem(12px);
}

.input {
  padding: 0 rem(24px);
  
  width: 100%;
  height: rem(68px);

  color: color(black);
  font-weight: 400;
  @include fsl(20px, 120%);
  letter-spacing: -0.02em;
  border-radius: rem(12px);
  background-color: #F5F5F5;
  border: none;

  &::placeholder {
    color: color(black);
    font-family: $ff-main;
  }
}

.textarea {
  padding: rem(20px) rem(24px);
  
  width: 100%;
  height: rem(200px);

  resize: none;
  color: color(black);
  font-weight: 400;
  @include fsl(20px, 120%);
  letter-spacing: -0.02em;
  border-radius: rem(12px);
  background-color: #F5F5F5;
  border: none;

  &::placeholder {
    color: color(black);
    font-family: $ff-main;
  }
}

.inner {
  margin-top: rem(12px);

  display: flex;
  justify-content: space-between;
}

.icon {
  --pad: #{rem(20px)};
  position: absolute;
  top: var(--pad);
  right: var(--pad);
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;

  svg {
    --size: #{rem(48px)};

    width: var(--size);
    height: var(--size);

    path {
      stroke: color(black);
    }
  }
}