@import 'src/styles/helpers.scss';

.footer {
  padding-top: rem(100px);

  background-color: color(white);
}

.top {
  padding-bottom: rem(40px);
}

.row {
  display: flex;
}

.col {

  flex-shrink: 0;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 9);
  }
  &:nth-child(2) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: calc(#{$col-part-d} * 7 - #{$col-gap-d});
  }
}

.contacts {
  margin-bottom: rem(60px);
}

.wrapper {
  display: flex;

  &:not(:first-child) {
    margin-top: rem(20px);
  }
}

.contact {
  @include fsl(100px, 90%);
  letter-spacing: -0.04em;
}

.address {

  &:not(:first-child) {
    margin-top: rem(32px);
  }
}

.heading {
  margin-bottom: rem(8px);

  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}

.label {
  color: color(gray);
  font-weight: 400;
  letter-spacing: -0.02em;
  @include fsl(18px, 130%);
}

.inner {
  display: flex;
  justify-content: space-between;
}

.links {
  margin-bottom: rem(24px);

  display: flex;
}

.link {

  display: flex;
  align-items: center;

  &:not(:first-child) {
    margin-left: rem(20px);
  }
}

.icon {
  margin-right: rem(4px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(32px)};

    width: var(--size);
    height: var(--size);
  }
}

.text {
  @include fsl(32px, 90%);
  letter-spacing: -0.04em;
}

.grid {
  display: grid;
  grid-template-columns: 2fr 3fr 2fr;
}

.column {

  &.bottom {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
}

.item {

  display: flex;

  &:not(:first-child) {
    margin-top: rem(12px);
  }
}

.ref {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.img {
  margin-right: rem(12px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(48px)};

    width: var(--size);
    height: var(--size);
  }
}

.bot {
  padding-top: rem(40px);

  border-top: 1px solid color(borders);
}

.politics {
  display: flex;
  align-items: center;
}

.policy {
  width: calc(#{$col-part-d} * 2);

  font-weight: 400;
  letter-spacing: -0.02em;
  @include fsl(12px, 130%);
}

.made {
  color: color(gray);
  font-weight: 400;
  letter-spacing: -0.02em;
  @include fsl(12px, 130%);
}

.logo {
  margin: rem(100px) 0 rem(40px);

  display: flex;
  align-items: center;

  svg {
    width: 100%;
    height: auto;
  }
}