@import 'src/styles/helpers.scss';

.awards {
  position: relative;
  z-index: 0;
  padding: rem(62px) 0 rem(100px);

  background-color: #101010;
  overflow: hidden;
}

.wrapper {
  margin: 0 auto;
  max-width: rem(500px);
}

.count {
  color: color(white);
  letter-spacing: -0.06em;
  @include fsl(300px, 90%);
}

.caption {
  text-align: center;
  letter-spacing: -0.04em;
  color: color(gray);
  @include fsl(40px, 90%);
}

.bg {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -1;

  display: flex;
  align-items: center;
}

.grid {
  margin: rem(60px) auto 0;

  width: rem(1025px);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: rem(22px);
}

.item {
  position: relative;
  z-index: 0;

  height: rem(224px);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(120px)};

    width: var(--size);
    height: var(--size);
  }
}

.name {
  margin-bottom: rem(12px);

  letter-spacing: -0.02em;
  color: color(gray);
  @include fsl(18px, 120%);
}

.num {
  color: color(white);
  letter-spacing: -0.05em;
  @include fsl(48px, 90%);

  span {
    margin-right: rem(5px);
    display: inline-block;
    transform: translateY(rem(-4px));
  }
}

.img {
  position: absolute;
  z-index: -1;

  display: flex;
  align-items: center;

  &.left {
    left: 0;
    top: rem(-273px);

    width: rem(729px);
    height: rem(950px);
  }

  &.right {
    right: rem(87px);
    bottom: rem(-130px);

    width: rem(568px);
    height: rem(952px);
  }
}