@import 'src/styles/helpers.scss';

.container {
  position: relative;
  z-index: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: rem(20px);
  overflow: hidden;
}

.wrapper {
  position: absolute;
  
  width: rem(600px);
  height: rem(260px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.decor {
  --size: #{rem(20px)};
  position: absolute;

  width: var(--size);
  height: var(--size);
  display: flex;
  align-items: center;

  &:nth-child(1) {
    left: 0;
    top: 0;
  }
  &:nth-child(2) {
    right: 0;
    top: 0;

    transform: rotate(90deg);
  }
  &:nth-child(3) {
    right: 0;
    bottom: 0;

    transform: rotate(180deg);
  }
  &:nth-child(4) {
    left: 0;
    bottom: 0;

    transform: rotate(-90deg);
  }

  svg {
    width: 100%;
    height: 100%;
  }
}

.inner {
  padding: rem(20px);

  width: rem(220px);

  border-radius: rem(12px);
  background-color: color(white);
  cursor: pointer;
}

.text {
  letter-spacing: -0.02em;
  @include fsl(18px, 100%);
}

.icon {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  svg {
    --size: #{rem(24px)};

    width: var(--size);
    height: var(--size);
  }
}