@import 'src/styles/helpers.scss';

.header {
  position: fixed;
  left: 0;
  top: 0;
  // right: 0;

  width: 100vw;

  z-index: 1000;
  border-top: 4px solid color(black-alt);
}

.menu {
  margin: 0 auto;
  position: relative;
  z-index: 0;

  width: rem(176px);
  height: rem(48px);

  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    z-index: -1;
    transform: translateX(-50%);

    width: rem(216px);
    // width: rem(176px);
    height: rem(48px);

    // background-image: url("data:image/svg+xml,%3Csvg width='176' height='48' viewBox='0 0 176 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0H176C164.954 0 156 8.9543 156 20V33C156 41.2843 149.284 48 141 48H35C26.7157 48 20 41.2843 20 33V20C20 8.95431 11.0457 0 0 0Z' fill='%230B0B0B'/%3E%3C/svg%3E%0A");
    background-image: url("data:image/svg+xml,%3Csvg width='216' height='48' viewBox='0 0 216 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0H216H211.455C200.409 0 191.455 8.9543 191.455 20V33C191.455 41.2843 184.739 48 176.455 48H39.5455C31.2612 48 24.5455 41.2843 24.5455 33V20C24.5455 8.95431 15.5911 0 4.54545 0H0Z' fill='%230B0B0B'/%3E%3C/svg%3E%0A");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &.active {
    .icon {
      opacity: 1;
      pointer-events: all;
    }

    .inner {
      opacity: 0;
    }
  }

}

.icon {
  position: absolute;
  left: 0;
  top: 0;

  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  pointer-events: none;
  opacity: 0;
  transition: $transition;

  svg {
    --size: #{rem(30px)};

    width: var(--size);
    height: var(--size);
  }
}

.inner {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: $transition;
}

.text {
  margin-right: rem(12px);
  color: color(white);
  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}

.burger {
  position: relative;
  z-index: 0;

  width: rem(32px);
  height: rem(8px);

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;

    height: 2px;

    background-color: color(white);
  }

  &::before {
    top: 0;
  }
  &::after {
    bottom: 0;
  }
}

.container {
  position: absolute;
  top: 0;
  left: rem(40px);
  right: rem(40px);
  z-index: 1;
  padding-top: rem(62px);

  transform: translateY(-110%);
  transition: $transition;
  pointer-events: none;

  &.active {
    transform: none;
  }
}

.row {
  display: flex;
  align-items: center;
  pointer-events: all;
  // justify-content: space-between;
}

.col {

  &:nth-child(2) {
    margin-left: rem(280px);
  }
  &:nth-child(3) {
    margin-left: auto;
  }
}

.list {
  display: flex;
}

.item {

  display: flex;
  align-items: center;
  justify-content: center;

  &:not(:first-child) {
    margin-left: rem(8px);
  }

  svg {
    --size: #{rem(52px)};

    width: var(--size);
    height: var(--size);
  }
}

.link {
  padding: rem(18px) rem(32px);

  color: color(white);
  border-radius: rem(12px);
  background-color: color(black-alt);
  cursor: pointer;
  letter-spacing: -0.02em;
  @include fsl(18px, 90%);

  &:not(:first-child) {
    margin-left: rem(8px);
  }

}