@import 'src/styles/helpers.scss';

.button {
  position: relative;
  z-index: 0;
  padding: rem(20px);

  width: rem(210px);
  height: rem(100px);
  display: flex;
  justify-content: space-between;

  border-radius: rem(12px);
  background-color: color(blue);
  cursor: pointer;
  overflow: hidden;

  &::after {
    --size: #{rem(156px)};
    content: "";
    position: absolute;
    top: rem(50px);
    left: rem(110px);
    z-index: -1;

    width: var(--size);
    height: var(--size);

    border-radius: 50%;
    background: color(black);
    pointer-events: none;
    opacity: 0.7;
    filter: blur(47px);
  }

  &.small {

    height: rem(56px);
    align-items: center;
    justify-content: center;

    &::after {
      top: rem(10px);
    }

    .text {
      margin-right: rem(10px);
    }
  }

  &.big {
    width: rem(282px);
  }
}

.text {
  color: color(white);
  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}

.icon {
  display: flex;
  align-items: flex-end;
  justify-content: center;

  svg {
    --size: #{rem(20px)};

    width: var(--size);
    height: var(--size);
  }
}