@import 'src/styles/helpers.scss';

.section {
  position: relative;
  z-index: 0;
  padding: rem(40px) 0;

  border-bottom: 1px solid color(borders);
}

.row {
  display: flex;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;

  svg {
    width: rem(220px);
    height: auto;
  }
}

.title {
  
}

.wrapper {
  padding: 0 rem(15px) rem(10px);
  margin: 0 0 rem(-10px);
  position: relative;
  z-index: 0;

  display: inline-block;
  width: rem(870px);
  height: rem(100px);

  color: color(gray-alt);
  overflow: hidden;
}

.span {
  position: absolute;
  left: rem(5px);
  top: rem(10px);

  display: inline-block;

  white-space: nowrap;

  &.static {
    position: static;
  }
}

.list {
  margin-top: rem(46px);

  display: flex;

  counter-reset: index;
}

.item {
  counter-increment: index;

  width: calc(#{$col-part-d} * 3);
  display: flex;

  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}

.text {
  &::before {
    content: "0" counter(index);
    margin-bottom: rem(12px);

    display: block;

    text-align: center;
    color: color(gray-alt);
  }
}

