@import 'src/styles/helpers.scss';

.row {
  display: flex;
}

.col {
  flex-shrink: 0;

  &:nth-child(1) {
    width: calc(#{$col-part-d} * 4);
  }
  &:nth-child(2) {
    width: calc(#{$col-part-d} * 12 - #{$col-gap-d});
  }
}

.title {
  text-align: left;
}

.grid {
  margin-top: rem(60px);

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: rem(38px);
  column-gap: rem(22px);
}

.wrapper {
  margin-top: rem(80px);
  margin-left: calc(#{$col-part-d} * 4);
  display: flex;
}

.list {
  flex-shrink: 0;
  width: calc(#{$col-part-d} * 3);
}

.item {
  // position: relative;
  // z-index: 0;

  // height: rem(160px);
  display: flex;
  align-items: center;
  // justify-content: center;

  &:not(:first-child) {
    margin-top: rem(20px);
  }
}

.text {
  // position: relative;
  // z-index: 0;

  letter-spacing: -0.02em;
  @include fsl(24px, 90%);
}

.num {
  margin-left: rem(12px);
  display: inline-block;
  // position: absolute;
  // left: calc(100% + #{rem(4px)});
  // bottom: calc(100% - #{rem(4px)});

  letter-spacing: -0.02em;
  color: color(gray-second);
  @include fsl(18px, 90%);
}

.input {
  padding: 0 rem(24px);
  margin-left: rem(30px);
  margin-bottom: rem(30px);

  width: rem(200px);
  height: rem(68px);

  color: color(black);
  font-weight: 400;
  @include fsl(20px, 120%);
  letter-spacing: -0.02em;
  border-radius: rem(12px);
  background-color: #F5F5F5;
  border: none;
}