@import 'src/styles/helpers.scss';

.label {
  position: relative;
  z-index: 0;
  padding: 0 rem(28px);

  display: flex;
  align-items: center;
  height: rem(56px);

  cursor: pointer;
  border: 2px solid color(borders);
  border-radius: rem(12px);
}

.input {
  position: absolute;
  left: 0;

  opacity: 0;
  pointer-events: none;
}

.icon {
  margin-right: rem(10px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(20px)};

    width: var(--size);
    height: var(--size);
  }
}

.text {
  @include fsl(18px, 90%);
  letter-spacing: -0.02em;
}