@import 'src/styles/helpers.scss';

.header {
  --pad: #{rem(40px)};
  position: fixed;
  left: 0;
  // left: var(--pad);
  // right: var(--pad);
  top: var(--pad);
  z-index: 1000;
  padding: 0 var(--pad);

  width: 100vw;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    width: rem(217px);
    height: rem(21px);
  }
}

.link {
  padding: 0 rem(28px);

  display: flex;
  align-items: center;
  height: rem(56px);

  border: 2px solid color(borders);
  border-radius: rem(12px);
  background-color: color(white);
}

.icon {
  margin-right: rem(10px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(20px)};

    flex-shrink: 0;
    width: var(--size);
    height: var(--size);

    transform: rotate(180deg);

    path {
      fill: color(black);
    }
  }
}

.text {
  letter-spacing: -0.02em;
  @include fsl(18px, 90%);
}