@import 'src/styles/helpers.scss';

.container {
  padding: rem(100px) 0;

  background-color: #101010;
}

.wrapper {
  margin: 0 auto;
  max-width: rem(600px);
}

.title {
  margin-top: rem(24px);

  color: color(white);
  letter-spacing: -0.04em;
  text-align: center;
  @include fsl(200px, 90%);
}

.row {
  display: flex;
  gap: rem(22px);

  &.center {
    gap: rem(32px);
    justify-content: center;
  }

  &:not(:first-child) {
    margin-top: rem(22px);
  }
}

.item {
  position: relative;
  z-index: 0;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  height: rem(427px);
  width: rem(443px);
  flex-shrink: 0;
}

.icon {
  margin-bottom: rem(12px);

  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    --size: #{rem(210px)};

    width: var(--size);
    height: var(--size);
  }
}

.heading {
  margin-bottom: rem(20px);

  max-width: rem(331px);

  text-align: center;
  color: color(white);
  @include fsl(40px, 90%);
  letter-spacing: -0.04em;
}

.text {
  max-width: rem(331px);

  font-weight: 400;
  text-align: center;
  color: #888888;
  line-height: 150%;
  letter-spacing: -0.02em;
}