@import 'src/styles/helpers.scss';

.icon {
  position: fixed;
  right: rem(40px);
  bottom: rem(40px);
  z-index: 100;

  display: flex;
  align-items: center;

  border-radius: rem(24px);
  box-shadow: 0px 29px 32px rgba(0, 0, 0, 0.3);
  
  svg {
    --size: #{rem(100px)};

    width: var(--size);
    height: var(--size);
  }
}