@import 'src/styles/helpers.scss';

.body {

  &--hidden {
    overflow: hidden;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;
  overflow-x: hidden;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.child {
  display: flex;
  align-items: center;
}